/* Tooltip text */
.tooltip-tag .tooltip-tag-text {
  padding: 5px;
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 0px;
  line-height: normal;
  font-size: smaller;

  /* Position the tooltip text - see examples below! */
  bottom: 100%;
  left: 50%;
  margin-left: -60px; /* Use half of the width (120/2 = 60), to center the tooltip */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip-tag:hover .tooltip-tag-text {
  visibility: visible;
}

.record-body {
  border: none;
}

.drop-row {
  margin-left: -10px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.drop-wrapper {
  border: none;
  position: relative;
}

.drop-wrapper .rs-item-quantity {
  position: absolute;
  left: 4px;
  top: -4px;
}

.drop-wrapper .card-img-top {
  padding: 2px;
}

.rs-item-quantity-number {
  display: inline-block;
  background-image: url('/img/numbers.png');
  height: 10px;
}

.rs-item-quantity-K {
  -webkit-filter: hue-rotate(60deg) brightness(10);
          filter: hue-rotate(60deg) brightness(10);
}

.rs-item-quantity-M {
  -webkit-filter: hue-rotate(90deg) brightness(0.8);
          filter: hue-rotate(90deg) brightness(0.8);
}

.rs-icon,
.rs-item-quantity-number {
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -o-pixelated;
      image-rendering: pixelated;
  image-rendering: crisp-edges;
}

